tem>\n                    </v-list-item-group>\n                  </v-list>\n                </div>\n              </v-list-group>\n              <v-list-item-group v-else :class=\"setTheme()\">\n                <v-list-item @click=\"clickRow(item2)\" id=\"listItem2\" v-model=\"item2.active\">\n                  <v-list-item-icon v-if=\"isIconPathAvaliable(item2.imagePath) ||\n                    isImagePathAvaliable(item2.imagePath)\n                    \">\n                    <v-icon v-if=\"isIconPathAvaliable(item2.imagePath)\" v-text=\"item2.imagePath\"\n                      v-bind=\"setIconSize(item2.options)\"></v-icon>\n                    <v-img v-if=\"isImagePathAvaliable(item2.imagePath)\" :src=\"item2.imagePath\"\n                      v-bind=\"setIconSize(item2.options)\"></v-img>\n                  </v-list-item-icon>\n                  <template v-if=\"item2.tooltip === true || item2.tooltip === 'true'\">\n                    <v-tooltip bottom>\n                      <template v-slot:activator=\"{ on, attrs }\">\n                        <v-list-item-content :id=\"item2.code\" v-bind=\"attrs\" v-on=\"on\">\n                          <v-list-item-title :style=\"`padding-left:${calculatePadding(1, item2)}px`\" v-text=\"item2.code\">\n                          </v-list-item-title>\n                        </v-list-item-content>\n                      </template>\n                      <span>{{ item2.code }}</span>\n                    </v-tooltip>\n                  </template>\n                  <template v-else>\n                    <v-list-item-content :id=\"item2.code\">\n                      <v-list-item-title :style=\"`padding-left:${calculatePadding(1, item2)}px`\" v-text=\"item2.code\"></v-list-item-title>\n                    </v-list-item-content>\n                  </template>\n                </v-list-item>\n              </v-list-item-group>\n            </v-list>\n          </div>\n        </v-list-group>\n\n        <v-list-item-group v-el