 miniVariantWidth: {\n      type: String,\n    },\n    menuData: {\n      type: [Object],\n    },\n    collapseOnItemSelect: {\n      type: Boolean,\n      default: false,\n    },\n    disabled: {\n      type: Boolean,\n      default: false,\n    },\n    hideMenuTitle: {\n      type: Boolean,\n      default: false,\n    },\n    fixed: {\n      type: Boolean,\n    },\n    color: {\n      type: String,\n    },\n    autoCollapse: {\n      type: Boolean,\n    },\n    title: {\n      type: String,\n    },\n    showOnlyIcon: {\n      type: Boolean,\n      default: false,\n    },\n    expandOnHover: {\n      type: Boolean,\n      default: false,\n    },\n  },\n  watch: {\n    menuData: {\n      immediate: true,\n      handler(newValue) {\n        this.resultList = newValue ?? [];\n        this.childrenSetActiveDefaultAsFalse(this.resultList);\n      },\n    },\n    disabled: {\n      immediate: true,\n      handler(newValue) {\n        if(newValue){\n          this.classMenu = \"v-navigation-drawer--disabled\";\n        }\n        else{\n          this.classMenu = undefined;\n        }\n      },\n    },\n    color: {\n      immediate: true,\n      handler(newValue) {\n        this.setTheme();\n      },\n    },\n    mini: {\n      immediate: true,\n      handler(newValue) {\n        this.$emit(\"update:mini-variant\", newValue);\n      },\n    },\n  },\n  mounted() {\n    if (this.expandOnHover) {\n      (<any>this.$refs.navigator).$el.onmouseleave = () => {\n        this.collapseAll();\n      }\n    }\n  },\n  destroyed() {\n    this.$el?.remove();\n  },\n  methods: {\n    setTheme() {\n      switch (this.activeColor) {\n        case \"primary\":\n          return \"primaryTheme\";\n        case \"secondary\":\n          return \"secondaryTheme\";\n        case \"success\":\n          return \"successTheme\";\n        case \"error\":\n          return \"errorTheme\";\n        case \"warning\":\n          return \"warningTheme\";\n        default:\n          if (this.color == undefined) {\n            return \"secondaryTheme\";\n          } else {\n            return \"white--text\";\n          }\n      }\n    },\n\n    isIconPathAvaliable(imagePath: any) {\n      let isAvaliable =\n        imagePath && imagePath.startsWith(\"mdi-\") ? true : false;\n      return isAvaliable;\n    },\n    isImagePathAvaliable(imagePath: any) {\n      let isAvaliable =\n        imagePath &&\n          (imagePath.toLocaleLowerCase(\"tr\").startsWith(\"http:\") ||\n            imagePath.toLocaleLowerCase(\"tr\").startsWith(\"https:\"))\n          ? true\n          : false;\n      return isAvaliable;\n    },\n    setPrependIcon(imagePath: any) {\n      return imagePath && imagePath.startsWith(\"mdi-\") ? imagePath : \"\";\n    },\n    setIconSize(iconPath: any) {\n      return {\n        size: iconPath?.iconOptions?.size,\n        \"x-small\": !!iconPath?.iconOptions?.xSmall,\n        small: !!iconPath?.iconOptions?.small,\n        large: !!iconPath?.iconOptions?.large,\n        \"x-large\": !!iconPath?.iconOptions?.xLarge,\n        color: iconPath?.iconOptions?.color\n      };\n    },\n    setImageSize(imagePath: any) {\n      return {\n        height: imagePath?.imageOptions?.height,\n        width: imagePath?.imageOptions?.width,\n      };\n    },\n    childrenSetActiveDefaultAsFalse(list) {\n      list.forEach((item: Object) => {\n        if (\n          Object(item).imagePath &&\n          !Object(item).imagePath.startsWith(\"mdi-\")\n        ) {\n          if (\n            !Object(item)\n              .imagePath.toLocaleLowerCase(\"tr\")\n              .startsWith(\"http:\") &&\n            !Object(item).imagePath.toLocaleLowerCase(\"tr\").startsWith(\"https:\")\n          ) {\n            let protocolAndPath =\n              window.location.protocol + \"//\" + window.location.host;\n            this.$set(\n              item,\n              \"imagePath\",\n              protocolAndPath +\n              (Object(item).imagePath.startsWith(\"/\")\n  